import React, { useState } from 'react';
import { Text, View, StyleSheet, Image, TouchableOpacity, Linking, Alert, ScrollView } from 'react-native';
import stylesheet from '../StyleSheet';
import { useFonts } from 'expo-font';
import AppLoading from 'expo-app-loading';
import { TextInput } from 'react-native-paper';
import MaterialIcons from '@expo/vector-icons/MaterialCommunityIcons'
const Contact = () => {
    const [name,setName] = useState("")
    const [email,setEmail] = useState("")
    const [subject, setSubject] = useState("")
    const [message, setMessage] = useState("")
    let [fontsLoaded] = useFonts({
        "mulish": require("../assets/fonts/mulish.ttf"),
    })

    if (!fontsLoaded) {
        return <AppLoading />
    }


    

    const openURI = async (urlStr) => {
        const url = urlStr
        const supported = await Linking.canOpenURL(url);
        if (supported) {
            await Linking.openURL(url);
        } else {
            Alert.alert(`Don't know how to open this URL: ${url}`);
        }
    }

    return (
        <ScrollView style={{backgroundColor:'#1F1F21', width:'100%', height:'100%'}}>
            <View style={{
                alignItems: 'center', flex: 1, width: '100%', height: '100%',
                marginTop: 50
            }}>
                <Text style={{
                    marginTop: 20, color: 'white', fontWeight: 'bold',
                    fontFamily: 'mulish', fontSize: 27
                }}>
                    CONTACT US
                </Text>

                <Text style={{
                    fontFamily: 'mulish', textAlign: 'center', color: 'white', padding: 20, marginTop: 20
                }}>Email me on mail : pankajisrani17@gmail.com or fill the form below and click on Send Email
                    Button to send me an {"\n"}email regarding any of your queries or for any work related topic.
                    {"\n\n"}You can contact me on Instagram too @idkpankaj
                </Text>
                <TouchableOpacity onPress={() => { openURI("https://instagram.com/idkpankaj/") }}>
                    <View style={{
                        width: 50, height: 50, borderRadius: 15, borderWidth: 2,
                        borderColor: 'white', alignItems: 'center', justifyContent: 'center'
                    }}>
                        <Image source={{ uri: "https://cdn-icons-png.flaticon.com/512/174/174855.png" }}
                            style={{ width: "80%", height: '80%' }}
                        />
                    </View>
                </TouchableOpacity>
                <View style={{ width: '100%', alignItems: 'center' }}>
                    <View style={{ flexDirection: 'row', alignItems:'center', marginTop:30 }}>
                        <View style={{
                            width: 100, height: 30, backgroundColor: 'black', alignItems: 'center',
                            justifyContent: 'center', marginTop:7, borderRadius:5
                        }}>
                            <Text style={{ color: 'white', fontFamily: 'mulish', alignSelf:'center' }}>Your Name</Text>
                        </View>
                        <TextInput placeholder='Name' style={{width:200,alignSelf:'center', height:30, backgroundColor:'transparent',
                        marginStart:10}}
                            mode='outlined' textColor='white' cursorColor='white' outlineColor='white'
                            value={name} onChangeText={(text)=>{setName(text)}}
                        />
                    </View>



                    <View style={{ flexDirection: 'row', alignItems:'center', marginTop:15 }}>
                        <View style={{
                            width: 100, height: 30, backgroundColor: 'black', alignItems: 'center',
                            justifyContent: 'center', marginTop:7, borderRadius:5
                        }}>
                            <Text style={{ color: 'white', fontFamily: 'mulish', alignSelf:'center' }}>
                            Email
                            </Text>
                        </View>
                        <TextInput placeholder='Email' style={{width:200,alignSelf:'center', height:30, backgroundColor:'transparent',
                        marginStart:10}}
                            mode='outlined' textColor='white' cursorColor='white' outlineColor='white'
                            value={email} onChangeText={(text)=>{setEmail(text)}}/>
                    </View>


                    <View style={{ flexDirection: 'row', alignItems:'center', marginTop:15 }}>
                        <View style={{
                            width: 100, height: 30, backgroundColor: 'black', alignItems: 'center',
                            justifyContent: 'center', marginTop:7, borderRadius:5
                        }}>
                            <Text style={{ color: 'white', fontFamily: 'mulish', alignSelf:'center' }}>
                            Subject
                            </Text>
                        </View>
                        <TextInput placeholder='Subject' style={{width:200,alignSelf:'center', height:30, backgroundColor:'transparent',
                        marginStart:10}}
                            mode='outlined' textColor='white' cursorColor='white' outlineColor='white'
                            value={subject} onChangeText={(text)=>{setSubject(text)}}/>
                    </View>


                    <View style={{ flexDirection: 'row', alignItems:'center', marginTop:15 }}>
                        <View style={{
                            width: 100, height: 30, backgroundColor: 'black', alignItems: 'center',
                            justifyContent: 'center', marginTop:7, borderRadius:5
                        }}>
                            <Text style={{ color: 'white', fontFamily: 'mulish', alignSelf:'center' }}>
                            Message
                            </Text>
                        </View>
                        <TextInput placeholder='Message' style={{width:200,alignSelf:'center', backgroundColor:'transparent',
                        marginStart:10}}
                        multiline
                            mode='outlined' textColor='white' cursorColor='white' outlineColor='white'
                            value={message} onChangeText={(text)=>{setMessage(text)}}/>
                    </View>

                    <TouchableOpacity style={{marginTop:20}} onPress={()=>{
                        if(name == "" || email == "" || subject == "" || message == ""){
                            alert("All fields are mandatory!")
                        }else{
                            alert("Check the mail on next page and send when done!")
                            openURI(`https://mail.google.com/mail/u/0/?fs=1&to=pankajisrani17@gmail.com&su=${subject}&body=${"Name : "+name+" "+
                            "Email : "+email+" "+"Message :  "+message}&tf=cm`)
                        }
                    }}>
                        <View style={{padding:20,alignItems:'center',justifyContent:'center', 
                        borderRadius:15, borderWidth:2, borderColor:'white'}}>
                            <Text style={{color:'white', fontFamily:'mulish'}}>
                                Send Email
                            </Text>
                        </View>
                    </TouchableOpacity>

                </View>
            </View>
        </ScrollView>
    )
}
//https://mail.google.com/mail/u/0/?fs=1&to=EMAIL&su=SUBJECT&body=BODYText&tf=cm

export default Contact